* {
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
}

h2 {
  text-align: center;
  color: #444;
}

:root {
  --game-grid-max-width: 660px;
  --board-width: min(100vw - 30px, var(--game-grid-max-width));
}

#board {
  container-type: inline-size;
}

.board-grid {
  --gutter-size: 10px;
  --cell-side: calc((var(--board-width)  - (var(--gutter-size) * 4)) / 5);
  --unselected-background: white;
  --selected-background: #eee;
  --selected-winning-background: #abffd5;
  --font-size: 1em;
  --cell-padding: 5px;
  --cell-border-radius: 5px;
  width: var(--board-width);
  grid-column-gap: var(--gutter-size);
  grid-row-gap: var(--gutter-size);
  -webkit-user-select: none;
  user-select: none;
  font-size: var(--font-size);
  -webkit-hyphens: auto;
  hyphens: auto;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  place-items: center;
  margin: 0 auto;
  display: grid;
}

@container (max-width: 400px) {
  .board-grid {
    --gutter-size: 2px;
    --cell-padding: 2px;
    --font-size: .8em;
    --cell-border-radius: 1px;
  }
}

.board-grid .cell {
  word-wrap: break-word;
  width: var(--cell-side);
  height: var(--cell-side);
  text-align: center;
  background-color: var(--unselected-background);
  border-radius: var(--cell-border-radius);
  filter: drop-shadow(2px 4px 3px #ddd);
  outline: 1px solid #d3d3d3;
  display: block;
  position: relative;
}

@media (hover) {
  .board-grid .cell:hover:not(.bonus) {
    transition: transform .2s;
    transform: translateY(-5px);
  }
}

.board-grid .cell.selected {
  background-color: var(--selected-background);
}

.board-grid .cell.winning {
  background-color: var(--selected-winning-background);
}

.board-grid .cell .word {
  pointer-events: none;
  padding: var(--cell-padding);
  width: auto;
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
}

#vocabularySelector {
  text-align: center;
  margin: 30px 0;
}

#vocabularySelector textarea {
  width: var(--board-width);
  min-height: 100px;
}

